import React, { Component } from "react";

import { Container } from "react-bootstrap";

import { Fade } from "react-reveal";

import SkillData from "./data/SkillData";

import { Button, Icon, Label, Card, Image } from "semantic-ui-react";

// https://codepen.io/drehimself/pen/QNXpyp

export class Me extends Component {
  constructor(props) {
    super(props);

    this.state = {
      navBarHeight: 0,
    };
  }

  componentDidMount() {
    this.setState({
      navBarHeight: document.getElementById("navbar").clientHeight,
    });
  }

  render() {
    const darkMode = this.props.dm;
    const textColor = this.props.dm ? "text-light" : "text-dark";
    const bgColor = this.props.dm ? "bg-dark" : "bg-light";

    return (
      <Container
        className="h-100"
        style={{
          paddingTop: this.state.navBarHeight,
          paddingBottom: this.state.navBarHeight / 2,
        }}
        fluid
      >


      </Container>
    );
  }
}

export default Me;
