import React from "react";
import Container from "react-bootstrap/Container";
import Card from "react-bootstrap/Card";
import Accordion from "react-bootstrap/Accordion";
import Badge from "react-bootstrap/Badge";

import { BiWorld } from "react-icons/all";

import Fade from "react-reveal/Fade";
import { Animated } from "react-animated-css";

import data from "./data/ProjectData";

function Projects(props) {
  const darkMode = props.dm;
  const ContainerFadeDelay = 150;

  const projectData = () => {
    var returnData = data.sort(function (a, b) {
      var dateA = new Date(a.date_start);
      var dateB = new Date(b.date_start);
      if (dateA > dateB) {
        return -1;
      } else if (dateA === dateB) {
        return 0;
      } else {
        return 1;
      }
    });
    for (let index = 0; index < returnData.length; index++) {
      returnData[index]["key"] = index;
    }
    return returnData;
  };

  function GetProjectWithinMonths(OverDate, WithinDate) {
    var returnObject = [];
    for (let index = 0; index < projectData().length; index++) {
      const element = projectData()[index];
      var monthDiffNum = monthDiff(new Date(element.date_start), new Date());
      if (monthDiffNum >= OverDate && monthDiffNum <= WithinDate) {
        returnObject.push(element);
      }
    }
    return returnObject;
  }

  function monthDiff(d1, d2) {
    var months;
    months = (d2.getFullYear() - d1.getFullYear()) * 12;
    months -= d1.getMonth();
    months += d2.getMonth();
    return months <= 0 ? 0 : months;
  }

  return (
    <Container
      className="h-100 d-flex align-items-center custom-card-container"
      fluid="md"
    >
      <div id="custom-card-column">
        <AccordionContainer
          topText="New"
          ContainerFadeDelay={ContainerFadeDelay}
          darkMode={darkMode}
          data={GetProjectWithinMonths(0, 2)}
        />
        <AccordionContainer
          topText="Over 2 Months"
          ContainerFadeDelay={ContainerFadeDelay}
          darkMode={darkMode}
          data={GetProjectWithinMonths(3, 5)}
        />
        <AccordionContainer
          topText="Over 6 Months"
          ContainerFadeDelay={ContainerFadeDelay}
          darkMode={darkMode}
          data={GetProjectWithinMonths(6, 9)}
        />
        <AccordionContainer
          topText="Over 10 Months"
          ContainerFadeDelay={ContainerFadeDelay}
          darkMode={darkMode}
          data={GetProjectWithinMonths(10, 9999)}
        />
      </div>
    </Container>
  );
}

function AccordionContainer(props) {
  return (
    <Accordion defaultActiveKey="0">
      <DividerContainer darkMode={props.darkMode}>
        <Animated
          animationIn="fadeIn"
          animationInDelay={props.ContainerFadeDelay}
        >
          {props.topText}
        </Animated>
      </DividerContainer>
      {props.data.map((obj, index) => (
        <ProjectCard
          obj={obj}
          key={obj.key}
          darkMode={props.darkMode}
          index={index}
          delay={props.ContainerFadeDelay + obj.key * 50}
        ></ProjectCard>
      ))}
    </Accordion>
  );
}

function DividerContainer(props) {
  return (
    <div
      className={`pt-1 pb-1 mt-1 mb-1 bg-${
        props.darkMode ? "dark" : "light"
      } text-info border-secondary border-bottom`}
    >
      {props.children}
    </div>
  );
}

function ProjectCard(props) {
  const date_start = props.obj.date_start;
  const date_end = props.obj.date_end;
  const description = props.obj.desc;
  const project_title = props.obj.project_local.title;
  const job_title = props.obj.job;
  const badgeLangs = props.obj.langs;
  const web_url = props.obj.project_local.local;
  const web_url_exist = web_url ? true : false;
  const darkMode = props.darkMode;
  return (
    <Animated animationIn="fadeIn" animationInDelay={props.delay}>
      <Card
        bg={darkMode ? "dark" : "light"}
        text={darkMode ? "light" : "dark"}
        className=""
      >
        <Accordion.Toggle as={Card.Header} eventKey={`"${props.index}"`}>
          <div className="w-100 d-flex justify-content-between">
            <span>{project_title}</span>
            <span>
              {web_url_exist ? (
                <a href={web_url}>
                  <BiWorld className="link-css" />
                </a>
              ) : null}
            </span>
          </div>
        </Accordion.Toggle>
        <Accordion.Collapse eventKey={`"${props.index}"`}>
          <Card.Body
            style={{ backgroundColor: darkMode ? "#495057" : "#DCDCDC" }}
          >
            <Card.Title className="">{job_title}</Card.Title>
            {badgeLangs.map((lang, index) => (
              <Badge pill className="mr-1" key={index} variant="success">
                {lang}
              </Badge>
            ))}
            <Badge pill className="mr-1" variant="primary">
              {date_start} - {date_end}
            </Badge>
            <hr />
            <Card.Text>{description}</Card.Text>
          </Card.Body>
        </Accordion.Collapse>
      </Card>
    </Animated>
  );
}

export default Projects;
