import React, { useState, useEffect } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Fade from "react-reveal/Fade";
import Col from "react-bootstrap/Col";

import { Button, Icon, Label, Flag } from "semantic-ui-react";

function Home(props) {
  const ContainerFadeDelay = 250;
  const bd = new Date("February 06, 1998");
  const ie = new Date("January 01, 2018");
  const darkMode = props.dm;
  const colors = [
    "Red",
    "Orange",
    "Yellow",
    "Olive",
    "Green",
    "Teal",
    "Blue",
    "Violet",
    "Purple",
    "Pink",
    "Grey",
  ];
  const badges = [
    {
      title: "Projects",
      subTitle: "23+",
      icon: "fork",
      color: "green",
      inverted: false,
    },
    {
      title: "Projected Grade",
      subTitle: "1-1",
      icon: "book",
      color: "green",
      inverted: false,
    },
    {
      title: "Industry Experience",
      subTitle: `${yearDiff(ie)} years`,
      icon: "industry",
      color: "green",
      inverted: false,
    },
  ];

  function yearDiff(date1) {
    return new Date(new Date() - new Date(date1)).getFullYear() - 1970;
  }

  useEffect(() => {
    console.log(document.getElementById("navbar").clientHeight);
  });

  return (
    <Container
      className={`text-${
        props.dm ? "light" : "dark"
      } h-100 d-flex align-items-center`}
      fluid
    >
      <Container>
        <Fade delay={ContainerFadeDelay}>
          <div className="p-1 m-0">
            <HomeText
              class="main-text p-1 text-center titillium-text"
              text="Theo Clapperton"
            />
            <SubText title="Full Stack Developer" />
            <HomeText
              class="lead-text text-center p-1"
              text={`I'm a Software Engineer based in Manchester, Currently studying at
              Manchester Metropolitan University.`}
            />
          </div>
          <Row className="p-1 m-0 position-relative d-flex justify-content-center align-items-center">
            {badges.map((obj, index) => (
              <Badge
                obj={obj}
                darkMode={darkMode}
                key={index}
                delay={400 + ContainerFadeDelay + index * 200}
              />
            ))}
            <Fade delay={400 + ContainerFadeDelay + badges.length * 200} top>
              <Badge
                obj={{
                  title: "lvl",
                  subTitle: (
                    <div className="age-text">
                      {yearDiff(bd)} <Flag name="uk" />
                    </div>
                  ),
                  icon: "star",
                  color: colors[
                    Math.floor(Math.random() * colors.length)
                  ].toLowerCase(),
                  inverted: true,
                }}
              />
            </Fade>
          </Row>
        </Fade>
      </Container>
    </Container>
  );
}
function HomeText(props) {
  return <div className={props.class}>{props.text}</div>;
}

function SubText(props) {
  return (
    <div className="sub-text p-1 text-center titillium-text">
      <span className="bracket">{"{"}</span> {props.title}{" "}
      <span className="bracket">{"}"}</span>
    </div>
  );
}

function Badge(props) {
  return (
    <div className="badge-gutter h-100 text-light">
      <Fade delay={props.delay} className="w-100 h-100" top>
        <Button as="div" labelPosition="right">
          <Button inverted={props.obj.inverted} color={props.obj.color}>
            <Icon name={props.obj.icon} />
            {props.obj.title}
          </Button>
          <Label as="span" basic pointing="left">
            {props.obj.subTitle}
          </Label>
        </Button>
      </Fade>
    </div>
  );
}

export default Home;
